import React from "react";
import "../css/calculator.css"

function Calculator() {
    // const value = "1"
    const[value,setValue] = React.useState("0")
    // const setValue = (key) => {
        // alert(key)
        // document.getElementById('setValue-area').value=key;
        // setValue(value+key)
    // }

    const calculator = () =>{
    try{
        const result =(eval (value));
        setValue(result)
       }catch (e){
        console.log(e);
        alert('invalid expression')
       }
}
  return (
  <div className="calculator">
        <div>
            <input id="setValue-area" type="text" value={value}/>
        </div>

        <div>
            <input type="button" value="1" className="button1" onClick={() =>setValue(value+"1")}/>
            <input type="button" value="2" className="button1" onClick={() =>setValue(value+"2")}/>
            <input type="button" value="3" className="button1" onClick={() =>setValue(value+"3")}/>
            <input type="button" value="4" className="button1" onClick={() =>setValue(value+"4")}/>
        </div>

        <div>
            <input type="button" value="5" className="button1" onClick={() =>setValue(value+"5")}/>
            <input type="button" value="6" className="button1"onClick={() =>setValue(value+"6")}/>
            <input type="button" value="7" className="button1" onClick={() =>setValue(value+"7")}/>
            <input type="button" value="8" className="button1" onClick={() =>setValue(value+"8")}/>
        </div>
        <div>
            <input type="button" value="9" className="button1" onClick={() =>setValue(value+"9")}/>
            <input type="button" value="0" className="button1" onClick={() =>setValue(value+"0")}/>
            <input type="button" value="+" className="button1" onClick={() =>setValue(value+"+")}/>
            <input type="button" value="-" className="button1" onClick={() =>setValue(value+"-")}/>
        </div>

        <div>
            <input type="button" value="*" className="button1" onClick={() =>setValue(value+"*")}/>
            <input type="button" value="/" className="button1" onClick={() =>setValue(value+"/")}/>
            <input type="button" value="." className="button1" onClick={() =>setValue(value+".")}/>
            <input type="button" value="AC" className="button1" onClick={() =>setValue(value+"AC")}/>
        </div>

        <div>
            <input type="button" value="clear" className="button2" onClick={() =>setValue("clear")}/>
            <input type="button" value="=" className="button2" onClick={() => calculator()}/>
        </div>
    </div>
  )
}
export default Calculator;