import React from "react";
import"../css/navbar.css"
import {NavLink} from "react-router-dom"
const Navbar = () => {
  return (
    <nav>
      <ul>
        <li>
          <NavLink to="/Home"><i className="fa-solid fa-house"></i> Home</NavLink>
        </li>
        <li>
          <NavLink to="/login"><i className="fa-solid fa-box"></i> Login</NavLink>
        </li>
        <li>
          <NavLink to="/signup"> <i className="fa-solid fa-user-plus"></i> Signup</NavLink>
        </li>
        <li>
          <NavLink to="/Employeelist"><i className="fa-solid fa-address-book"></i>Employeelist</NavLink>
        </li>
        {/* <li>
          <NavLink to="/EmployeeDetails">EmployeeDetails</NavLink>
        </li> */}
        <li>
          <NavLink to="/PagiNation"><i className ="fa-solid fa-file"></i>PagiNation</NavLink>
        </li>
        <li>
          <NavLink to="/Calculator"> <i className="fa-solid fa-calculator"></i> Calculator</NavLink>
        </li>
        <li>
        <NavLink to="/Dummy"> <i className="fa-solid fa-calculator"></i> Dummy</NavLink>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
