import React from "react";
import profile from "../assets/blank pic.png"


class Cardclass extends  React.Component {
    constructor (props){
        super(props)
    }
    render(){
        return(
            <div className="emp-card-template">
      <img src={profile} />
      <div className="emp-">
        <p className="emp-name">{this.props.emp.Name}</p>
        <p className="emp-id">{this.props.emp.EmployeeID}</p>
        <button onClick={() => this.props.btnFn()}>View</button>
      </div>
    </div>
        )
    }     
}            

export default Cardclass;