import React from "react";

function Thead(props) {
  const { keys } = props;
  return (
    <thead>
      <tr>
        {/* <th>User Id</th>
            <th>Id</th>
            <th>Title</th>
            <th>Body</th> */}
        {keys.map((e) => (
          <th key={e}>{e}</th>
        ))}
      </tr>
    </thead>
  );
}

export default Thead;
