import React from "react";
import profile from "../assets/blank pic.png";

function Card(props) {
  console.log(props);
  return (
    <div className="emp-card-template">
      <img src={profile} />
      <div className="emp-">
        <p className="emp-name">{props.emp.Name}</p>
        <p className="emp-id">{props.emp.EmployeeID}</p>
        <button onClick={() => props.btnFn()}>View</button>
      </div>
    </div>
  );
}

exportdefaultCard;