import React  from 'react';
import Card from '../components/card';
import {useNavigate} from "react-router-dom";
import { useSelector } from "react-redux";

function EmployeeDetails(){
    const navigate = useNavigate()
    // const employee = {
    //     Name: "Jonas",
    //     Age: 60,
    //     Email: "interdum.Curabitur@laoreet.edu",
    //     "Date of joining": "04/04/2018",
    //     "Phone number": "1604120243099",
    //     Street: "455-1121 Orci. Road",
    //     City: "Kizilcahamam",
    //     Zip: "3074",
    //     Region: "Ank",
    //     Country: "Bahamas",
    //     Info: "lectus. Nullam suscipit, est ac facilisis facilisis, magna tellus faucibus",
    //     EmployeeID: 189,
    //   };

    const employee = useSelector(state => state.employee.employee)
    return(
        <div>
           <h1> EmployeeDetails </h1>
            <Card emp={employee} btnFn={() => navigate('/EmployeeList')} btnName="back"></Card>
            <table>
                {Object.keys(employee).map(e =>(
                    <tr>
                        <th>
                            {e}
                        </th>
                        <td>{employee[e]}</td>
                    </tr>
                ))}
            </table>
        </div>
    )
}

export default EmployeeDetails